<template>
  <div class="wrapper">
    <!-- 事件描述 -->
    <div class="items">
      <!-- <div class="title">
        <div class="img"></div>
      </div> -->
      <v-title title="事件描述"></v-title>
      <div class="content">
        <ul>
          <li v-for="(data, index) in eventInfo" :key="index">
            <label>{{ data.name }}</label>
            <label>{{ data.value }}</label>
          </li>
        </ul>
        <div class="dizhen">
          <div class="top" @click="more">更多</div>
        </div>
      </div>
    </div>
    <!-- 任务展示 -->
    <div class="items">
      <!-- <div class="title">
        <div class="img"></div>
      </div> -->
      <v-title title="任务展示"></v-title>
      <div class="content">
        <ul>
          <li
            v-for="(list, index) in task"
            :key="index"
            :class="[
              {
                ulyellow: 1 === list.state,
                ulgreen: 2 === list.state,
                ulgreen: 3 === list.state
              }
            ]"
          >
            <label>☆</label>
            <label>{{ list.name }}</label>
          </li>
        </ul>
      </div>
    </div>
    <div class="items">
      <div
        class="box"
        v-for="(item, index) in process"
        :key="index"
        :class="{ active: true === item.state }"
      >
        <label></label>
        <span>{{ item.name }}</span>
      </div>
    </div>
    <!-- 地图工具 -->
    <div class="items">
      <map-tool></map-tool>
    </div>
    <!-- 视频监控 -->
    <div class="items">
      <!-- <div class="title">
        <div class="img"></div>
      </div> -->
      <v-title title="视频监控"></v-title>
      <div class="content">
        <div class="video-img">
          <div
            class="imgs"
            v-for="(imgs, index) in imgData"
            :key="index"
            :style="{ background: 'url(' + imgs.imgurl + ')' }"
          ></div>
        </div>
      </div>
    </div>
    <!-- 处置规程 -->
    <div class="items">
      <!-- <div class="title">
        <div class="img"></div>
      </div> -->
      <v-title title="处置规程"></v-title>
      <div class="content">
        <ul>
          <li v-for="(data, index) in rulesExplain" :key="index">
            <label
              style="display: inline-block;width:5px;height:16px;background:#51d0ff;border-radius: 10px;"
            ></label>
            <label>{{ data.name }}</label>
          </li>
        </ul>
      </div>
    </div>
    <!-- 倒计时 -->
    <div class="event-time">
      <span>00:04:35</span>
    </div>
  </div>
</template>

<script>
import Acontent from '../../components/Acontent'
import Mcontent from '../../components/Mcontent'
import Maptools from '../../components/Maptools'
import Title from '../../components/Title'
import postEvent from '../../api/event'
export default {
  data () {
    return {
      isActive: true,
      eventInfo: [
        {
          name: '发生时间:',
          value: '2019年10月29日13:15:42'
        },
        {
          name: '事件类型:',
          value: '地震'
        },
        {
          name: '事发地址:',
          value: '和平区'
        },
        {
          name: '事件内容:',
          value: '发生地震，现场急需救援。'
        },
        {
          name: '事件来源:',
          value: '预警信息'
        }
      ],
      task: [
        { name: '已上报 地址灾害救援司 ', state: 1 },
        { name: '已签收 救援司', state: 1 },
        { name: '已反馈 应急指挥中心', state: 2 }
      ],
      // 处置规程
      rulesExplain: [
        { name: '与受灾区域取得联系' },
        { name: '核实受灾区域面积和人口' },
        { name: '了解通往受灾区域的交通情况' },
        { name: '及时电话联系震区每一家危化企业' },
        { name: '开展环境风险与地质隐患排查工作' },
        { name: '了解通往受灾区域的交通情况' },
        { name: '及时电话联系震区每一家危化企业' },
        { name: '核实受灾区域面积和人口' },
        { name: '与受灾区域取得联系' },
        { name: '核实受灾区域面积和人口' },
        { name: '了解通往受灾区域的交通情况' },
        { name: '及时电话联系震区每一家危化企业' },
        { name: '开展环境风险与地质隐患排查工作' },
        { name: '了解通往受灾区域的交通情况' },
        { name: '及时电话联系震区每一家危化企业' },
        { name: '核实受灾区域面积和人口' }
      ],
      process: [
        { name: '事件接报', state: true },
        { name: '先期处置', state: true },
        { name: '预案响应', state: true },
        { name: '指挥调度', state: false },
        { name: '指挥结束', state: false }
      ],
      layerId: '',
      imgData: [
        { imgurl: require('../../assets/images/home/vimg01.png') },
        { imgurl: require('../../assets/images/home/vimg02.png') },
        { imgurl: require('../../assets/images/home/vimg03.png') },
        { imgurl: require('../../assets/images/home/vimg04.png') },
        { imgurl: require('../../assets/images/home/vimg05.png') },
        { imgurl: require('../../assets/images/home/vimg06.png') },
        { imgurl: require('../../assets/images/home/vimg03.png') },
        { imgurl: require('../../assets/images/home/vimg04.png') },
        { imgurl: require('../../assets/images/home/vimg05.png') },
        { imgurl: require('../../assets/images/home/vimg06.png') }
      ],
      eventId: 'abcd', // 事件编号
      sendEvent: {} // 更多,传递事件详情
    }
  },
  computed: {
    printTime: function () {
      return this.eventInfo[0].value.replace('-', '年').value.replace('-', '月').value.replace(' ', '日').slice(0, this.eventInfo[0].value.length - 2)
    }
  },
  methods: {
    openLayerEvent () {
      let id = this.$layer.iframe({
        content: {
          content: Acontent, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: { eventId: this.eventId }// props
        },
        shade: false, // 是否显示遮罩
        area: ['400px', '420px'],
        offset: ['1300', '560'],
        title: '事件分析'
      })
      this.layerId = id
    },
    initEventMap () {
      this.$map.drawPointToMap({
        x: 117.15889948750838,
        y: 39.241615658216425,
        ico: { name: './static/arcgismap/assets/img/yy.png', height: 30, width: 30 },
        layerId: 'resoulayer',
        isCenter: true,
        click: () => { }
      })

      this.$map.drawPointToMap({
        x: 117.23031543766851,
        y: 39.23607613363374,
        ico: { name: './static/arcgismap/assets/img/dzjg.png', height: 30, width: 30 },
        layerId: 'resoulayer',
        isCenter: true,
        click: () => { }
      })

      this.$map.drawPointToMap({
        x: 117.17914175665761,
        y: 39.21638986120756,
        ico: { name: './static/arcgismap/assets/img/wz.png', height: 30, width: 30 },
        layerId: 'resoulayer',
        isCenter: true,
        click: () => { }
      })

      this.$map.drawPointToMap({
        x: 117.20603666050805,
        y: 39.24846220867363,
        ico: { name: './static/arcgismap/assets/img/hgc.png', height: 30, width: 30 },
        layerId: 'resoulayer',
        isCenter: true,
        click: () => { }
      })

      this.$map.clearLayers(this.$map.constslayer.alarmLayer)
      this.$map.clearLayers(this.$map.constslayer.cacheLayer)
      this.$map.drawCircle({
        x: 117.2009,
        y: 39.2243,
        radius: 1500,
        layerId: this.$map.constslayer.cacheLayer,
        isCenter: false,
        style: {
          color: '#000000',
          fontSize: 12,
          lineStyle: 'solid',
          borderWidth: 1,
          borderColor: '#f82026',
          background: [248, 32, 38, 0.65],
          fillStyle: 'cross'
        },
        click: () => { this.openLayerEvent() },
        onsuccess: (wkt, opt) => {
          this.$map.drawPointToMap({
            x: opt.x,
            y: opt.y,
            ico: { name: './static/arcgismap/assets/img/marker.png', height: 30, width: 22 },
            layerId: this.$map.constslayer.alarmLayer,
            isCenter: true,
            click: () => { }
          })
        }
      })
    },
    // 更多
    more () {
      let id = this.$layer.iframe({
        content: {
          content: Mcontent, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: {eventInfo: this.sendEvent}// props
        },
        shade: false, // 是否显示遮罩
        area: ['400px', '420px'],
        offset: ['700', '350'],
        title: '事件详情'
      })
      this.layerId = id
    },
    async getEventInfo () {
      var table = {
        DataBase: 'yjzh',
        DynamicTable: {
          Conditions: [{ Property: 'Id', Oper: 'eq', Value: this.eventId }],
          TableData: null,
          TableName: 'Event',
          WorkType: 'Get'
        }
      }
      const event = await postEvent(table)
      if (event.Result === 0) {
        if (event.Data.length > 0) {
          let eventInfo = event.Data[0]
          this.eventInfo[0].value = eventInfo.Fssj
          this.eventInfo[1].value = eventInfo.Category
          this.eventInfo[2].value = eventInfo.Fsdd
          this.eventInfo[3].value = eventInfo.Content
          this.eventInfo[4].value = '电话接报'
          this.eventInfo[0].value = this.printTime
          eventInfo.Fssj = this.eventInfo[0].value
          this.sendEvent = eventInfo
          console.log(this.sendEvent)
        }
      }
    }
  },
  mounted () {
    this.openLayerEvent()
    this.initEventMap()
    this.getEventInfo()
  },
  components: {
    'map-tool': Maptools,
    'v-title': Title
  }
}
</script>

<style lang="stylus" scoped>
body {
  .my-class {
    .layui-layer {
      border: 2px solid red;
    }

    .layui-layer-title {
      background: #c00;
      color: #fff;
      border: none;
    }
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: space-between;
  width: 100%;
  height: 90%;
  pointer-events: none;
  position: relative;

  // background red
  .items {
    width: 20%;
    height: 31%;
    // background: #008000;
    margin: 14px 14px 0px 20px;
    pointer-events: auto;
  }

  .items:nth-child(1) {
    width: 17%;

    .title {
      .img {
        width: 204px;
        height: 52px;
        background: url('../../assets/images/home/shijianmiaoshu.png') no-repeat;
      }
    }

    .content {
      width: 94%;
      height: 78%;
      border: solid 1px #11c2ff;
      background: rgba(17, 194, 255, 0.1);
      color: #fff;
      font-size: 16px;

      ul li {
        padding: 0px 0px 0px 16px;
        height: 30px;
        line-height: 30px;
        margin: 6px 0 0 0px;
      }

      .dizhen {
        width: 100%;
        height: 56%;
        // background: url('../../assets/images/home/jingweidu.png') no-repeat;
        margin: 10px 0 0 16px;

        .top {
          width: 48px;
          height: 24px;
          background: rgba(3, 47, 142, 0.5);
          color: #00F1E5;
          line-height: 24px;
          text-align: center;
          margin: -16px 0 0 220px;
          cursor: pointer;
        }
      }
    }
  }

  .items:nth-child(2) {
    width: 17%;
    height: 40%;

    .title {
      .img {
        width: 180px;
        height: 52px;
        background: url('../../assets/images/home/renwuzhanshi.png') no-repeat;
      }
    }

    .content {
      width: 96%;
      height: 104%;
      margin: -6px 0 0 0px;
      color: #FFFFFF;
      overflow-y: scroll;

      // background: url('../../assets/images/home/left_two.png') no-repeat;
      // background-size 100% 100%

      // background-size 100% 100%
      ul li {
        padding: 0px 0px 0px 16px;
        border: 1px solid #1D7295;
        width: 290px;
        height: 34px;
        line-height: 34px;
        margin: 6px 0 0 0px;
        background-color: rgba(6, 44, 65, 0.5);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        cursor: pointer;
      }

      li:hover {
        background: rgba(158, 131, 36, 0.5);
        color: #FFCB1E;
        border: 1px solid #9E8324;
      }

      .ulyellow {
        background: rgba(158, 131, 36, 0.5);
        color: #fff;
        border: 1px solid #9E8324;
      }

      .ulgreen {
        background: rgba(12, 168, 107, 0.5);
        color: #fff;
        border: 1px solid #08A467;
      }

      .ulblue {
        background: rgba(44, 133, 164, 0.5);
        color: #fff;
        border: 1px solid #2C85A4;
      }
    }
  }

  .items:nth-child(3) {
    display: flex;
    height: 20%;
    width: 74%;
    // margin: 0 0 0 18%;
    position: relative;
    top: 8%;
    left: 28%;
    text-align: center;

    .box {
      width: 232px;
      height: 104px;
      background: url('../../assets/images/home/bottom_blue_bj.png') no-repeat;
      position: relative;
      left: -14%;
      color: #fff;

      label {
        display: block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #f00;
        margin: 30px 0 0 -4px;
      }

      span {
        font-size: 24px;
        line-height: 64px;
        font-family: 'yang-myfont';
      }
    }

    .active {
      color: #ff5534;
      background: url('../../assets/images/home/bottom_juse_bj.png') no-repeat;
    }
  }

  .items:nth-child(4) {
    height: 12%;
    margin: 0 0 0 -21%;
    position: relative;
  }

  // 视频监控
  .items:nth-child(5) {
    height: 46%;
    margin: -5% 0px 0px 12px;

    .title {
      .img {
        width: 200px;
        height: 50px;
        background: url('../../assets/images/home/shipingjiankong.png') no-repeat;
      }
    }

    .content {
      .video-img {
        width: 320px;
        height: 348px;
        // background: url('../../assets/images/home/videoimg.png') no-repeat;
        margin: -14px 0 0 55px;
        background-size: 100% 100%;
        cursor: pointer;
        overflow-y: scroll;

        .imgs {
          width: 141px;
          height: 103px;
          background: url('../../assets/images/home/vimg01.png') no-repeat;
          float: left;
          margin: 0 16px 18px 0px;
        }
      }
    }
  }

  .items:nth-child(6) {
    height: 40%;

    .title {
      .img {
        width: 200px;
        height: 50px;
        background: url('../../assets/images/home/chuzhiguicheng.png') no-repeat;
        margin: 44px 0 0 0px;
      }
    }

    .content {
      width: 82%;
      height: 82%;
      border: solid 1px #11c2ff;
      background: rgba(17, 194, 255, 0.1);
      color: #fff;
      font-size: 16px;
      margin: 0 0 0 14%;
      overflow-y: scroll;

      ul li {
        padding: 0px 0px 0px 16px;
        height: 34px;
        line-height: 34px;
        margin: 6px 0 0 0px;
      }
    }
  }

  .event-time {
    width: 244px;
    height: 94px;
    background: url('../../assets/images/home/daojishiimg.png') no-repeat;
    position: absolute;
    top: 2%;
    left: 22%;
    text-align: center;
    line-height: 124px;

    span {
      font-size: 28px;
      color: #fff;
      font-family: 'djs-myfont';
    }
  }
}
</style>



